import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, FormArray, Validators, FormControl } from '@angular/forms';
import { matchPass } from 'src/app/core/validatetion/validate';

@Component({
  selector: 'app-learn-form-reactive',
  templateUrl: './learn-form-reactive.component.html',
  styleUrls: ['./learn-form-reactive.component.scss']
})
export class LearnFormReactiveComponent implements OnInit {

  register: FormGroup;

  constructor(
    private fb: FormBuilder
  ) { }

  ngOnInit() {
    this.register = this.fb.group({
      fullname: ['', Validators.required],
      email: [''],
      pass: [''],
      confirmPass: [''],
      add: this.fb.group({
        street: [''],
        city: ['']
      }),
      skills: this.fb.array([
        this.fb.control(''),
        this.fb.control('')
      ])
    }, {
      validator: matchPass('pass', 'confirmPass')
    });

    setTimeout(() => {
      const results = {
        fullname: 'David DOng',
        email: 'hodong@gmail.com'
      };
    }, 2000);

    this.register.valueChanges.subscribe(e => {
      console.log(this.register);
    });
  }

  action() {
    console.log(this.register);
  }

  get skills() {
    return this.register.controls['skills'] as FormArray;
  }

  get fullname() {
    return this.register.controls['fullname'] as FormControl;
  }

  addSkill() {
    this.skills.push(this.fb.control(''));
  }

}
