import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LearnFormComponent } from './learn-form.component';
import { LearnFormTemplateComponent } from './learn-form-template/learn-form-template.component';
import { LearnFormReactiveComponent } from './learn-form-reactive/learn-form-reactive.component';

const routes: Routes = [
  {
    path: '',
    component: LearnFormComponent,
    children: [
      {
        path: 'template',
        component: LearnFormTemplateComponent
      },
      {
        path: 'reactive',
        component: LearnFormReactiveComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class LearnFormRoutingModule { }
